.container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    margin-left: 15px;
    height: 15px;
    cursor: pointer;
    flex-grow: 1;
}


.circle {
    font-size: 1.5rem;
    font-weight: 300;
    margin: 0;
    padding: 0;
}