.languageSkills {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
    padding-bottom: 35px;
}

.languageSkills>div:nth-child(2) {
    flex-grow: 0.5;
    justify-content: start;
    margin-left: 30px;
}

.buttonsContainer {
    z-index: 2;
    width: 100%;
    display: flex;
    justify-content: center;
    column-gap: 10px;
    position: absolute;
    top: 80px;
}

.button {
    font-family: inherit;
    height: 30px;
    width: 30px;
    border-radius: 0;
    /*color: #656666*/
    vertical-align: middle;
    outline: none;
    font-size: 1.4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(255, 218, 68);
    border-radius: 4px;
    border: none;
    transition: .4s;
}


.button:hover {
    cursor: pointer;
    background-color: rgb(253, 240, 120);
}