/* container for both label and select */
.selectContainer {
    display: flex;
    justify-content: center;
    margin-top: 25px;
    margin-bottom: 15px;
}

.label {
    display: flex;
    justify-content: right;
    align-items: center;
    text-align: right;
    flex-basis: 150px;
    max-width: 150px;
    flex-grow: 1;
    flex-shrink: 1;
    font-size: 1.1rem;
    font-family: inherit;
}


.select {
    height: 40px;
    border: 1px solid #d9d9d6;
    font-family: inherit;
    font-size: 1.1rem;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='38' viewBox='0 0 24 24' width='28' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 100%;
    background-position-y: 50%;
    padding-left: 9px;
    margin-left: 15px;
    /* flex-basis: 350px; */
    max-width: 350px;
    flex-shrink: 1;
    flex-grow: 1;
}

.select:focus {
    border: 1px solid #d9d9d6;
    outline: none;
}