textarea::placeholder {
    font-family: "NouvelR-Light", sans-serif;
    color: #817f7f;
}

.container {
    display: flex;
    justify-content: center;
    margin-top: 25px;
    margin-bottom: 10px;
    font-size: 1.1rem;
    flex-grow: 1;
}

.label {
    display: inline-block;
    text-align: right;
    flex-basis: 150px;
    width: 150px;
    font-family: inherit;
}

.textAreaWrapper {
    max-width: 350px;
    flex-basis: 350px;
    margin-left: 15px;
}

.input {
    display: flex;
    box-sizing: border-box;
    height: 120px;
    padding: 16px 8px;
    font-family: inherit;
    resize: vertical;
    outline: none;
    font-size: 1.1rem;
    min-height: 100px;
    max-height: 520px;
}

.inputError {
    display: flex;
    width: 100%;
    box-sizing: border-box;
    height: 120px;
    padding: 16px 8px;
    font-family: inherit;
    resize: vertical;
    /*transition: .8s;*/
    outline: none;
    font-size: 1.1rem;
    min-height: 100px;
    max-height: 520px;

}

.lengthStatus {
    font-size: 12px;
    text-align: right;
    margin-top: 5px;
    margin-right: 5px;
}