.mainDataContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.mainData {
    width: 58%;
    margin-top: 5px;
}

.photoContainer {
    margin: auto;
    margin-top: 23px;
    width: 32%;
    /* margin-left: 3%; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: left;
}

.image {
    width: 90%;
    height: auto;
    outline: none;
}

/* load photo - button to upload applicant's photo */

.loadPhoto {
    margin: 20px;
    width: 150px;
    height: 40px;
    font-family: inherit;
    padding: 5px;
    border-radius: 0;
    outline: none;
    font-size: 1.1rem;
    background-color: rgb(255, 218, 68);
    border-radius: 4px;
    border: none;
    transition: .4s;
}

.loadPhoto:hover {
    cursor: pointer;
    background-color: rgb(252, 229, 137);
}

